.section_pharmacy {
    background-color: #f3fff6;
    padding-top: 35px;
    padding-bottom: 50px;
    overflow: hidden;
    @media screen and (min-width: 768px) {
        padding-top: 35px;
        padding-bottom: 35px;
    }
    @media screen and (min-width: 1200px) {
        padding-top: 47px;
        padding-bottom: 40px;
    }
    @media screen and (min-width: 1440px) {
        padding-top: 76px;
        padding-bottom: 74px;
    }
    @media screen and (min-width: 1920px) {
        padding-top: 86px;
    }
}

.background_paws {
    @media screen and (min-width: 1200px) {
        background-repeat: no-repeat;
        background-image: url(../images/background-images/pharmacy/paws_top_left.svg),
            url(../images/background-images/pharmacy/paws_bottom_right.svg);
        background-size: 131px, 153px;
        background-position: top 40px left 100px, bottom right 110px;
    }
    @media screen and (min-width: 1440px) {
        background-size: 157px, 184px;
        background-position: top 120px left 47px, bottom right 148px;
    }
    @media screen and (min-width: 1920px) {
        background-size: auto;
        background-position: top 90px left 110px, bottom right 170px;
    }
}

.positon_title_thumb_descr {
    margin-bottom: 20px;
    @media screen and (min-width: 768px) {
        margin-bottom: 30px;
    }
    @media screen and (min-width: 1200px) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
    }
    @media screen and (min-width: 1440px) {
        margin-bottom: 60px;
        padding-bottom: 30px;
    }
}

.title_pharmacy {
    color: #04a427;
    font-family: var(--font-Manrope);
    letter-spacing: -0.01;
    font-size: 15px;
    line-height: 1.1;
    @media screen and (min-width: 768px) {
        max-width: 90px;
    }
    @media screen and (min-width: 1200px) {
        max-width: 105px;
        font-size: 17px;
    }
    @media screen and (min-width: 1440px) {
        max-width: 120px;
        font-size: 20px;
    }
}

.descr_pharmacy {
    text-transform: uppercase;
    line-height: 120%;
    letter-spacing: 0.02;
    font-family: var(--font-Manrope);
    color: #06631b;
    font-size: 19px;
    font-weight: 500;
    text-align: left;
    @media screen and (min-width: 768px) {
        font-size: 28px;
    }
    @media screen and (min-width: 1200px) {
        max-width: 620px;
        margin: 0 0 0 70px;
        font-size: 30px;
    }
    @media screen and (min-width: 1440px) {
        max-width: 830px;
        margin: 0 0 0 96px;
        font-size: 40px;
    }
    @media screen and (min-width: 1920px) {
        max-width: 1126px;
        margin: 0 0 0 55px;
        font-size: 43px;
    }
}

.descr_pharmacy span {
    color: #04a427;
}

.descr_pharmacy span:nth-child(1) {
    font-family: var(--font-Cormorant-Unicase);
    font-weight: 600;
    font-size: 22px;
    @media screen and (min-width: 768px) {
        font-size: 31px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 34px;
    }
    @media screen and (min-width: 1440px) {
        font-size: 45px;
    }
}

