        rgba(20, 114, 12, 0.04) 76.5px,
        rgba(20, 114, 12, 0.03) 76.5px,
        rgba(20, 114, 12, 0.03) 98px,
        rgba(20, 114, 12, 0.02) 98px,
        rgba(20, 114, 12, 0.02) 116px
    );
    @media screen and (min-width: 768px) {
        width: 562px;
        height: 562px;
        transform: translate(22px, 34px);

        background-image: radial-gradient(
            circle at center,
            rgba(20, 114, 12, 0.04),
            rgba(20, 114, 12, 0.04) 185px,
            rgba(20, 114, 12, 0.03) 185px,
            rgba(20, 114, 12, 0.03) 237px,
            rgba(20, 114, 12, 0.02) 237px,
            rgba(20, 114, 12, 0.02) 282px
        );
    }
    @media screen and (min-width: 1200px) {
        width: 602px;
        height: 602px;
        transform: translate(22px, 100px);
        background-image: radial-gradient(
            circle at center,
            rgba(20, 114, 12, 0.04),
            rgba(20, 114, 12, 0.04) 197px,
            rgba(20, 114, 12, 0.03) 197px,
            rgba(20, 114, 12, 0.03) 252px,
            rgba(20, 114, 12, 0.02) 252px,
            rgba(20, 114, 12, 0.02) 300px
        );
    }
    @media screen and (min-width: 1440px) {
        width: 722px;
        height: 722px;
        transform: translate(26px, 103px);
        background-image: radial-gradient(
            circle at center,
            rgba(20, 114, 12, 0.04),
            rgba(20, 114, 12, 0.04) 237px,
            rgba(20, 114, 12, 0.03) 237px,
            rgba(20, 114, 12, 0.03) 303px,
            rgba(20, 114, 12, 0.02) 303px,
            rgba(20, 114, 12, 0.02) 360px
        );
    }

    @media screen and (min-width: 1920px) {
        width: 894px;
        height: 894px;
        transform: translate(34px, 215px);
        background-image: radial-gradient(
            circle at center,
            rgba(20, 114, 12, 0.04),
            rgba(20, 114, 12, 0.04) 293px,
            rgba(20, 114, 12, 0.03) 293px,
            rgba(20, 114, 12, 0.03) 373px,
            rgba(20, 114, 12, 0.02) 373px,
            rgba(20, 114, 12, 0.02) 445px
        );
    }
}

