.btn_pharmacy_item {
    width: 100%;
    // flex-basis: calc(100%);
    // flex-basis: calc(100% - 10px / 2);
    font-size: 15px;
    height: 46px;
    border-radius: 10px;
    border-top: 1.5px solid rgba(4, 164, 39, 0.4);
    border-bottom: 1.5px solid rgba(4, 164, 39, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    @include animation;
    transition-property: color;
    font-family: var(--font-Manrope);
    @media screen and (min-width: 768px) {
        flex-basis: calc(100% - 10px / 2);
    }
    @media screen and (min-width: 1200px) {
        height: 52px;
        font-size: 17px;
    }

    @media screen and (min-width: 1440px) {
        flex-basis: calc(100% - 20px / 2);
        height: 62px;

        border-radius: 20px;
    }
    @media screen and (min-width: 1920px) {
        font-size: 18px;
        flex-basis: calc(100% - 20px / 2);
        height: 78px;
        border-radius: 20px;
    }
}

